import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Header from "./components/Header";
import Home from "./pages/Home";
import IndexUsers from "./pages/Users/Index";
import ShowUsers from "./pages/Users/Show";
import IndexPosts from "./pages/Posts/Index";
import ShowPosts from "./pages/Posts/Show";
import EditPost from "./pages/Posts/Edit";
import CreatePost from "./pages/Posts/Create";

function App() {
  return (
    <BrowserRouter>
      <Header />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/users" element={<IndexUsers />} />
        <Route path="/users/:Id" element={<ShowUsers />} />
        <Route path="/posts" element={<IndexPosts />} />
        <Route path="/posts/:Id" element={<ShowPosts />} />
        <Route path="/posts/edit/:Id" element={<EditPost />} />
        <Route path="/posts/create" element={<CreatePost />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
