import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import Delete from "../../components/DeletePost";

const ShowPosts = () => {
  const { Id } = useParams();
  const [post, setPost] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetch(`https://jsonplaceholder.typicode.com/posts/${Id}`)
      .then((res) => res.json())
      .then((data) => {
        setPost(data);
        setLoading(false);
        setError(null);
      })
      .catch((err) => {
        setError(err.message);
        setLoading(false);
      });
  }, [Id]);
  return (
    <div className="container mt-5">
      <div className="row g-3">
        {error && <div>{error}</div>}
        {loading && (
          <div className="spinner-border text-warning position-absolute top-50 start-50"></div>
        )}
        {post && (
          <div className="col-md-8">
            <div className="card">
              <div className="card-header">{post.title}</div>
              <ul className="card-body">{post.body}</ul>
              <div className="card-footer">
                <Delete postId={post.id} />
                <Link className="btn btn-sm btn-dark" to={`/posts/edit/${Id}`}>
                  Edit
                </Link>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default ShowPosts;
